body {
    font-family: 'open sans', sans-serif;
}

table {
    width: 100%;
    font-size: 11px;
    border-collapse: collapse;
}

thead th {
    text-align: left;
    border-bottom: 2px solid black;
}

thead button {
    border: 0;
    border-radius: none;
    font-family: inherit;
    font-weight: 700;
    font-size: inherit;
    padding: 0.5em;
    margin-bottom: 1px;
    cursor: ns-resize;
    outline: none;
    width: 100%;
    /* border-right: 1px solid black; */
}
thead button.ascending,
thead button.descending {
    color: #1890ff;
    font-weight: bold;
}
thead button::after {
    content: '⬇️';
    position: relative;
    visibility: hidden;
    display: inline-block;
    margin-left: 3px;
}

thead button.ascending::after {
    content: '⬇️';
    visibility: visible;
    display: inline-block;
    margin-left: 3px;
}

thead button.descending::after {
    content: '⬆️';
    visibility: visible;
    display: inline-block;
    margin-left: 3px;
}

tbody td {
    padding: 0.5em;
    border-bottom: 1px solid #ccc;
}

tbody tr:hover {
    background-color: #eee;
}

td {
    /* border-right: 1px solid; */
}
